body,
html {
    padding: 0;
    margin: 0;
    height: 100%;
}

#root {
    height: 100%;
}

.container {
    height: 100%;
}

.splash {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.splash__image {
    width: 200px;
    animation: tilt 3s infinite;
    opacity: .6;
    cursor: pointer;
}

.splash__image:hover {
    opacity: 1;
    transition: opacity .3s;
}

@keyframes tilt {

    0%,
    10%,
    50%,
    60% {
        transform: none;
    }

    5% {
        transform: rotate(-30deg) translateX(-10px)
    }

    55% {
        transform: rotate(30deg) translateX(10px);
    }
}

.background {
    position: fixed;
    width: 100%;
    z-index: 2;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
}

.background__bar {
    animation: fill 2s 1s ease-in forwards;
    background: black;
    width: 0;
}

.background__bar:nth-child(even) {
    align-self: flex-end
}

@keyframes fill {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

.battle {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px;
    box-sizing: border-box;
    font-family: "pokemon";
}

.battle__action {
    border: 20px double black;
    height: 20%;
    border-radius: 5px;
    padding: 1rem;
}

.battle__text {
    font-size: 3rem;
    text-transform: uppercase;
}

.battle__top {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    overflow: hidden;
}

.battle__user {
    display: flex;
    justify-content: flex-between;
    animation: slide 2s linear forwards, color 2s forwards;
    width: 100%;
}

.battle__trainer {
    transform: translateX(2000px);
    flex-direction: row-reverse;
}

.battle__trainer .battle__bar {
    align-self: center
}

.battle__trainer img {
    margin-right: auto
}

.battle__pokemon {
    align-self: flex-end;
    transform: translateX(-2000px);
}

.battle__pokemon img {
    height: 200px;
    margin-right: 5rem;
}

.battle__pokemon .battle__bar {
    margin-right: auto
}

.battle__bar {
    height: 50%;
    width: 600px;
    border-bottom: 10px solid black;
    border-right: 10px solid black;
    border-radius: 0 0 10px 0;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.battle__bar::before {
    content: "";
    width: 30px;
    height: 20px;
    background: black;
    position: absolute;
    bottom: -10px;
    left: -30px;
    clip-path: polygon(0 100%, 100% 0, 100% 100%);
}

.battle__pokemon .battle__bar {
    border-left: 10px solid black;
    border-right: 0;
    border-radius: 0 0 0 10px;
}

.battle__pokemon .battle__bar::before {
    left: auto;
    right: -30px;
    clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
}

.battle__pokeball {
    width: 30px;
    height: 30px;
    border: 5px solid black;
    margin-right: 20px;
    border-radius: 50%
}

.battle__name {
    font-size: 2rem;
    text-transform: uppercase;
}

.battle__hp {
    display: flex;
    width: 70%;
    border-bottom: 5px solid black;
    border-right: 15px solid black;
}

.hp__header {
    background: black;
    height: 30px;
    width: 50px;
    color: pink;
    display: flex;
    justify-content: center;
    align-items: center
}

.hp__bar {
    background: green;
    height: 10px;
    width: 100%;
    align-self: center
}

@keyframes slide {
    to {
        transform: none;
        filter: none;
    }
}

@keyframes color {

    0%,
    99% {
        filter: grayscale(1)
    }

    100% {
        filter: grayscale(0)
    }
}

.pokeball {
    width: 50px;
    height: 50px;
    background: linear-gradient(to bottom, red 50%, pink 100%);
    position: absolute;
    border-radius: 50%;
    border: 3px solid black;
}

.pokeball--throwing {
    animation: throw 1s forwards, falling 2s 1s forwards, tilt 2s 2s 3;
}

@keyframes throw {
    from {
        left: 0;
        bottom: 0;
    }

    to {
        left: 85%;
        bottom: 85%;
    }
}

.battle__pokemon .catching {
    transition: all .5s;
    transform: scale(0)
}

@keyframes falling {
    0% {
        top: 5%;
        left: 85%;
    }

    10% {
        top: 30%;
        left: 85%;
    }

    15% {
        top: 27%;
        left: 85%;
    }

    20% {
        top: 30%;
        left: 85%;
    }

    30% {
        top: 29%;
        left: 85%
    }

    to {
        top: 30%;
        left: 85%;
    }
}

.pokeball--catched {
    top: 30%;
    left: 85%;
    filter: grayscale(0)
}